<template>
    <div>
        <div class="box_table">
            <TableToolBar :formData="formSearch"  @addModal="addModal(1)">
                <el-input
                    slot="starttime"
                    size="mini"
                    placeholder=""
                    suffix-icon="el-icon-date"
                    v-model="formSearch.starttime">
                </el-input>
                <el-input
                    slot="endtime"
                    size="mini"
                    placeholder=""
                    suffix-icon="el-icon-date"
                    v-model="formSearch.endtime">
                </el-input>
                <div slot="addModal" class="box_add">
                    <el-button type="primary" icon="el-icon-circle-plus-outline" size="mini" @click="addModal"></el-button>
                </div>
            </TableToolBar>
            <el-table
                :data="tableData.filter(data => !search || data.batch.toLowerCase().includes(search.toLowerCase()))"
                :default-sort = "{prop: 'number', order: 'descending'}"
                stripe
                style="width: 100%">
                <el-table-column
                label="C"
                prop="batchC">
                </el-table-column>
                <el-table-column
                label=""
                prop="productName">
                </el-table-column>
                <el-table-column
                label=""
                prop="furnaceNumber">
                </el-table-column>
                <el-table-column
                label=""
                prop="productQuantity">
                </el-table-column>
                <el-table-column
                label=""
                prop="sinteringTime">
                </el-table-column>
                <el-table-column
                label=""
                prop="sinteringBox">
                </el-table-column>
                <el-table-column
                label="">
                    <template slot-scope="scope">
                        <el-dropdown>
                            <span class="el-dropdown-link">
                                <i class="el-icon-caret-bottom el-icon--right"></i>   
                            </span>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item><span @click="handleEdit(scope.$index, scope.row)"></span></el-dropdown-item>
                                <el-dropdown-item><span  @click="handleDelete(scope.$index, scope.row)"></span></el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </template>
                </el-table-column>
            </el-table>
            <Pagination></Pagination>
        </div>
        <!-- addEdit -->
        <addEditModal v-show="addVisible" :visible.sync="addVisible" :addOrEdit="addOrEdit" :formData="addOrEdit===1?{}:formModal" @handleModal="handleModal(arguments)" />
        
        <!-- delete -->
        <delModal v-show="delVisible" :visible.sync="delVisible" :rowId="rowId" @handleModalDel="handleModalDel(arguments)"></delModal>
    </div>
</template>
<script>
import TableToolBar from '../../../components/TableToolBar/TableToolBar.vue'
import addEditModal from './addEditModal.vue'
import delModal from '../../../components/ModalDelete/ModalDelete.vue'
import Pagination from '../../../components/Pagination/Pagination.vue'
export default {
    data() {
        return {
            // tableToolBar
            formSearch: {
                batch:'',
                starttime:'',
                endtime:''
            },
            search:'',
            // 
            tableData: [
                {
                batchC: 'APB-091',
                productName: '',
                furnaceNumber:'LH-801',
                productQuantity:'134,100',
                sinteringTime:'2019/05/09',
                sinteringBox:'BQ-130   BQ-130   BQ-130',
                },
                {
                batchC: 'APB-091',
                productName: '',
                furnaceNumber:'LH-801',
                productQuantity:'134,100',
                sinteringTime:'2019/05/09',
                sinteringBox:'BQ-130   BQ-130   BQ-130',
                },
                {
                batchC: 'APB-091',
                productName: '',
                furnaceNumber:'LH-801',
                productQuantity:'134,100',
                sinteringTime:'2019/05/09',
                sinteringBox:'BQ-130   BQ-130   BQ-130',
                }
            ],
            // 
            formModal: {
                batchC:'APB-091',
                productName:'',
                furnaceNumber:'LH-801',
                productQuantity:'134,100',
                sinteringTime:'2019/05/09',
                sinteringBox:'BQ-130   BQ-130   BQ-130'
            },
            addVisible: false, // ,
            delVisible: false, // 
            // index
            rowId:0,
            // addOrEdit  1 2
            addOrEdit: 1 
        }
    },
    methods: {
        //  
        handleEdit(index, row) {
            this.rowId = index
            this.addOrEdit = 2
            this.addVisible = true
        },
        //  
        handleDelete(index, row) {
            this.rowId = index
            this.delVisible = true
        },
        // 
        addModal () {
            this.addOrEdit = 1
            this.addVisible = true;
        },
        //  
        handleModal(param){
            if(param[0]) {
                if(param[1] === 1) {
                    console.log('')
                } else if(param[1] === 2) {
                    console.log('')
                }
            } else {
                console.log('')
            }
            this.addVisible = false
        },
        //  
        handleModalDel (param){
            if(param[0]) {
                console.log('',this.rowId)
            } else {
                console.log('')
            }
            this.delVisible = false
        }
    },
    components:{
        TableToolBar,
        addEditModal,
        delModal,
        Pagination
    }
}
</script>
<style lang="stylus" scope>
@import '../../../common/common.stylus'
</style>


