<template>
  <el-dialog
    width="600px"
    :visible.sync="visible"
    :before-close="handleModal"
    :close-on-click-modal="false"
    :close-on-press-escape="false">
    <div slot="title">{{addOrEdit === 1?'':''}}</div>
    <div>
      <el-form :label-position="labelPosition" label-width="80px">
        <el-form-item label="C:" size="small">
          <el-input v-model="formData.batchC"></el-input>
        </el-form-item>
        <el-form-item label=":" size="small">
          <el-input v-model="formData.productName"></el-input>
        </el-form-item>
        <el-form-item label=":" size="small">
          <el-input v-model="formData.furnaceNumber"></el-input>
        </el-form-item>
        <el-form-item label=":" size="small">
          <el-input v-model="formData.productQuantity"></el-input>
        </el-form-item>
        <el-form-item label=":" size="small">
          <el-input v-model="formData.sinteringTime"></el-input>
        </el-form-item>
        <el-form-item label=":" size="small">
          <el-input v-model="formData.sinteringBox"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div slot="footer" class="detail-wrap-bottom">
      <el-button type="default" @click="handleModal(false, addOrEdit)"></el-button>
      <el-button type="primary" @click="handleModal(true, addOrEdit)"></el-button>
    </div>
  </el-dialog>
</template>
<script>
  export default {
    props: {
      visible: {
        type: Boolean,
        default: false
      },
      formData:Object,
      addOrEdit:Number
    },
    data() {
      return {
        labelPosition: 'left'
      };
    },
    methods: {
      // isOperate , addOrEdit
      handleModal(isOperate, addOrEdit) {
        this.$emit('handleModal',isOperate, addOrEdit)
        // this.$emit('update:visible', false); // 
      }
    }
  }
</script>


