<template>
  <div class="box_table">
      <el-container>
        <el-header class="menu-header">
          <el-button-group>
            <el-button type="primary"
                       icon="el-icon-plus"
                       size="small"
                       @click.native="handleAdd"
                       v-if="currentUser.permission.sys_menu_btn_add"></el-button>
            <el-button type="primary"
                       icon="el-icon-edit"
                       size="small"
                       @click.native="handleEdit"
                       v-if="currentUser.permission.sys_menu_btn_edit"></el-button>
            <el-button type="primary"
                       icon="el-icon-delete"
                       size="small"
                       @click.native="handleDel"
                       v-if="currentUser.permission.sys_menu_btn_del"></el-button>
          </el-button-group>
        </el-header>
      </el-container>
      <el-container>
        <el-aside width="300px">
          <el-tree :data="menuAll"
                   node-key="id"
                   highlight-current
                   default-expand-all
                   :expand-on-click-node="false"
                   @node-click="handleNodeClick"></el-tree>
        </el-aside>
        <el-main>
          <el-form ref="form"
                   :model="form"
                   label-width="80px">
            <el-form-item label="ID">
              <el-input v-model="parentForm.id"
                        :disabled="true"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="parentForm.label"
                        :disabled="true"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="form.label"
                        :disabled="formGrade"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="form.icon"
                        :disabled="formGrade"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="form.href"
                        :disabled="formGrade"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary"
                         @click="handleSubmit"
                         :disabled="formGrade"
                         v-if="formStatus=='add'"></el-button>
              <el-button type="primary"
                         @click="handleSubmit"
                         :disabled="formGrade"
                         v-if="formStatus=='edit'"></el-button>
            </el-form-item>
          </el-form>
        </el-main>
      </el-container>
  </div>
</template>

<script>
  import { mapGetters } from "vuex";
  export default {
    name: "MenuView",
    data () {
      return {
        form: {},
        obj: {},
        parentForm: {},
        formGrade: true,
        formStatus: ""
      };
    },
    created () {
      //this.$store.dispatch("GetMenuAll").then(data => { });
    },
    mounted () {

    },
    computed: {
      ...mapGetters('CommonDictionaryStore',["menuAll"])
      ,...mapGetters('UserStore',["currentUser"])
    },
    props: [],

    methods: {

      handleNodeClick (data, checked, indeterminate) {
        this.parentForm = Object.assign({}, findParent(this.menuAll, data.id));
        this.formGrade = true;
        this.formStatus = "";
        this.obj = data;
        this.form = data;
      },
      handleAdd () {
        this.formGrade = false;
        this.formStatus = "add";
        this.form = {};
      },
      handleEdit () {
        if (validatenull(this.obj)) {
          this.$message({
            showClose: true,
            message: "",
            type: "warning"
          });
          return false;
        }
        this.form = Object.assign({}, this.obj);
        this.formStatus = "edit";
        this.formGrade = false;
      },
      handleDel () {
        this.$confirm(`${this.form.label}`, "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            this.$message({
              showClose: true,
              message: "",
              type: "success"
            });
          })
          .catch(err => { });
      },
      handleSubmit () { }
    }
  };
</script>

<style scoped>

</style>



