<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-main box_table" v-loading="loading" element-loading-text="">
      <!---->
      <div class="table_tool">
        <div class="box_search">
          <el-form ref="form" :model="filter">
            <el-input v-model="filter.batchNo" placeholder="" @keyup.enter.native="search" size="mini"></el-input>
            <el-date-picker v-model="filter.BeginDateTime" placeholder="" value-format="yyyy-MM-dd">
            </el-date-picker>
            <el-date-picker v-model="filter.EndDateTime" placeholder="" value-format="yyyy-MM-dd">
            </el-date-picker>
            <el-select v-model="filter.productId"  placeholder="">
              <el-option
                v-for="item in productOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
            <el-select v-model="filter.equipmentId"  placeholder="">
              <el-option
                v-for="item in equipmentOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
            <el-button type="primary" icon="el-icon-search" size="mini" @click="search" ></el-button>
            <el-button type="primary" icon="el-icon-circle-plus-outline" style="float: right" size="mini" @click="showAddDialog" ></el-button>
          </el-form>
        </div>
      </div>
      <!---->
      <el-table class="tableCss" :data="datalist" highlight-current-row @selection-change="selectInfos" stripe
                style="width: 100%;" :max-height="maxHeight">
        <el-table-column type="index" width="60">
        </el-table-column>
        <el-table-column prop="batchNo" label="">
        </el-table-column>
        <el-table-column prop="productName" label="" >
        </el-table-column>
        <el-table-column prop="equipmentName" label="">
        </el-table-column>
        <el-table-column prop="quantity" label="">
        </el-table-column>
        <el-table-column prop="beginDateTime" label="">
        </el-table-column>
        <el-table-column prop="endDateTime" label="">
        </el-table-column>
        <!--<el-table-column prop="description" label="">
        </el-table-column>-->
        <el-table-column label="" width="150">
          <template slot-scope="scope">
            <el-button size="small" @click="dimensionDetailjumpHtml(scope.row.batchNo)"></el-button>
            <el-button size="small" @click="dimensionSummaryjumpHtml(scope.row.batchNo)"></el-button>
            <!-- <el-button type="danger" @click="del(scope.row.taskID)" size="small"></el-button> -->
          </template>
        </el-table-column>
      </el-table>
