<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-main box_table" v-loading="loading" element-loading-text="">
      <!---->
      <div class="table_tool">
        <div class="box_search">
          <el-form ref="form" :model="filter">
           <!-- <el-input v-model="filter.productID" placeholder="" @keyup.enter.native="search" size="mini"></el-input>-->
            <el-select v-model="filter.productId"    placeholder="">
              <el-option
                v-for="item in productOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
            <el-select v-model="filter.workProcessId"  placeholder="">
              <el-option
                v-for="item in workProcessOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
           <!-- <el-input v-model="filter.workProcessID" placeholder="" @keyup.enter.native="search" size="mini"></el-input>-->
            <el-button type="primary" icon="el-icon-search" size="mini" @click="search" ></el-button>
            <el-button type="primary" icon="el-icon-circle-plus-outline" style="float: right"  size="mini" @click="showAddDialog" ></el-button>
          </el-form>
        </div>
      </div>
      <!---->
      <el-table class="tableCss" :data="datalist" highlight-current-row @selection-change="selectInfos" stripe
                style="width: 100%;">

        <el-table-column type="index" width="60">
        </el-table-column>
        <el-table-column prop="productName" label="" width="280" sortable>
        </el-table-column>
        <el-table-column prop="workProcessName" label=""  sortable>
        </el-table-column>
        <el-table-column prop="parameterName" label=""  sortable>
        </el-table-column>
        <el-table-column label="" width="180">
          <template slot-scope="scope">
            <el-button size="small" @click="showEditDialog(scope.row)"></el-button>
            <el-button type="danger" @click="del(scope.row.inspectionParamId)" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-col :span="24" class="toolbar">
        <Pagination
          @handleCurrentChange="handleCurrentChange"
          @handleSizeChange="maxResultCountSelect"
          :page-size="maxResultCount"
          :page-sizes="pageSelectList"
          :total="totalCount">
        </Pagination>
      </el-col>
      <!---->
      <el-dialog  :visible.sync ="addFormVisible"  :before-close="closeAddDialog">
        <div slot="title"></div>
        <el-form :model="addForm" label-width="100px" :rules="editFormRules" ref="addForm">
          <el-form-item label=":" prop="productId">
            <el-select v-model="addForm.productId"  placeholder="">
              <el-option
                v-for="item in productOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" prop="workProcessId">
            <el-select v-model="addForm.workProcessId"  placeholder="">
              <el-option
                v-for="item in workProcessOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" prop="parameterId">
            <el-select v-model="addForm.parameterId"  placeholder="">
              <el-option
                v-for="item in parameterOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="closeAddDialog"></el-button>
          <el-button type="primary" @click.native="add" :loading="loading"></el-button>
        </div>
      </el-dialog>

      <el-dialog  :visible.sync ="editFormVisible"   :before-close="closeEditDialog">
        <div slot="title"></div>
        <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
          <el-form-item label=":" prop="productId">
            <el-select v-model="editForm.productId"  placeholder="">
              <el-option
                v-for="item in productOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" prop="workProcessId">
            <el-select v-model="editForm.workProcessId"  placeholder="">
              <el-option
                v-for="item in workProcessOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" prop="parameterID">
            <el-select v-model="editForm.parameterId"  placeholder="">
              <el-option
                v-for="item in parameterOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="closeEditDialog"></el-button>
          <el-button type="primary" @click.native="edit"></el-button>
        </div>
      </el-dialog>
    </el-col>
  </el-row>
</template>
<script>
  import InspectionParamView from '../../../View/InspectionParamView';
  export default new InspectionParamView()
</script>
<style lang="stylus" scope>
  @import '../../../common/common.stylus'
</style>



