<template>
  <!--<div>
     <div class="box_table">
         <TableToolBar :formData="formSearch"  @addModal="addModal(1)">
             <el-form-item slot="input">
                 <el-input
                           size="mini"
                           placeholder=""
                           v-model="formSearch.defectItemName">
                 </el-input>
             </el-form-item>
             <div slot="addModal" class="box_add">
                 <el-button type="primary" icon="el-icon-circle-plus-outline" size="mini" @click="addModal"></el-button>
             </div>
         </TableToolBar>
         <el-table
             :data="tableData.filter(data => !search || data.batch.toLowerCase().includes(search.toLowerCase()))"
             :default-sort = "{prop: 'number', order: 'descending'}"
             stripe
             style="width: 100%">
             <el-table-column
             label=""
             prop="defectItemCode">
             </el-table-column>
             <el-table-column
             label=""
             prop="defectItemName">
             </el-table-column>
             <el-table-column
             label=""
             prop="memo">
             </el-table-column>
             <el-table-column
             label="">
                 <template slot-scope="scope">
                     <el-dropdown>
                         <span class="el-dropdown-link">
                             <i class="el-icon-caret-bottom el-icon--right"></i>   
                         </span>
                         <el-dropdown-menu slot="dropdown">
                             <el-dropdown-item><span @click="handleEdit(scope.$index, scope.row)"></span></el-dropdown-item>
                             <el-dropdown-item><span  @click="handleDelete(scope.$index, scope.row)"></span></el-dropdown-item>
                         </el-dropdown-menu>
                     </el-dropdown>
                 </template>
             </el-table-column>
         </el-table>
         <Pagination></Pagination>
     </div>

     <addEditModal v-show="addVisible" :visible.sync="addVisible" :addOrEdit="addOrEdit" :formData="addOrEdit===1?{}:formModal" @handleModal="handleModal(arguments)" />

     delete
        <delModal v-show="delVisible" :visible.sync="delVisible" :rowId="rowId" @handleModalDel="handleModalDel(arguments)"></delModal>
    </div>-->
  <el-row class="warp">
    <el-col :span="24" class="warp-main box_table" v-loading="loading" element-loading-text="">
      <!---->
      <div class="table_tool">
        <div class="box_search">
          <el-form ref="form" :model="filter">
            <el-input v-model="filter.defectItemKeyword" placeholder="" @keyup.enter.native="search" size="mini"></el-input>
            <el-button type="primary" icon="el-icon-search" size="mini" @click="search" ></el-button>
            <el-button type="primary" icon="el-icon-circle-plus-outline" style="float: right"  size="mini" @click="showAddDialog" ></el-button>
          </el-form>
        </div>
      </div>
      <!---->
      <el-table class="tableCss" :data="datalist" highlight-current-row @selection-change="selectInfos" stripe
                style="width: 100%;">

        <el-table-column type="index" width="60">
        </el-table-column>
        <el-table-column prop="defectItemCode" label="" width="280" sortable>
        </el-table-column>
        <el-table-column prop="defectItemName" label=""  sortable>
        </el-table-column>
        <el-table-column prop="memo" label=""  sortable>
        </el-table-column>
        <el-table-column label="" width="180">
          <template slot-scope="scope">
            <el-button size="small" @click="showEditDialog(scope.row)"></el-button>
            <el-button type="danger" @click="del(scope.row.defectItemID)" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-col :span="24" class="toolbar">
        <Pagination
          @handleCurrentChange="handleCurrentChange"
          @handleSizeChange="maxResultCountSelect"
          :page-size="maxResultCount"
          :page-sizes="pageSelectList"
          :total="totalCount">
        </Pagination>
      </el-col>
      <!---->
      <el-dialog  :visible.sync ="addFormVisible"  :before-close="closeAddDialog">
        <div slot="title"></div>
        <el-form :model="addForm" label-width="100px" :rules="editFormRules" ref="addForm">
          <el-form-item label=":" prop="defectItemCode">
            <el-input v-model="addForm.defectItemCode" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="defectItemName">
            <el-input v-model="addForm.defectItemName" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="memo">
            <el-input type="textarea" v-model="addForm.memo" auto-complete="off"></el-input>
          </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="closeAddDialog"></el-button>
          <el-button type="primary" @click.native="add" :loading="loading"></el-button>
        </div>
      </el-dialog>

      <el-dialog  :visible.sync ="editFormVisible"   :before-close="closeEditDialog">
        <div slot="title"></div>
        <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
          <el-form-item label=":" prop="defectItemCode">
            <el-input v-model="editForm.defectItemCode" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="defectItemName">
            <el-input v-model="editForm.defectItemName" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="memo">
            <el-input type="textarea" v-model="editForm.memo" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="closeEditDialog"></el-button>
          <el-button type="primary" @click.native="edit"></el-button>
        </div>
      </el-dialog>
    </el-col>
  </el-row>
</template>
<script>
  import DefectItemView from '../../../View/DefectItemView';
  export default new DefectItemView()
</script>
<style lang="stylus" scope>
  @import '../../../common/common.stylus'
</style>



