<template>
  <div :id="id" class="charts"></div>
</template>

<script>
  export default {
    data() {
      return {
      }
    },
    props:['width','height','id','xAxisValue','PValue','minValue','maxValue'],
    mounted(){
      this.drawLine();
    },
    methods: {
      drawLine(){
        let that=this;
        //
        let arrayNum =that.PValue.join(",").split(",");
        let PValueMax = Math.max.apply(null,arrayNum);
        //
        let range = [];
        that.xAxisValue.map(function(x){
          if(x > that.minValue && x < that.maxValue){
            range.push(x);
          }
        });
        //  
        //
        let MinArr = [];
        range.map(function(x){ MinArr.push(Math.abs(x - that.minValue));});//  
        let MinSubscript = MinArr.indexOf(Math.min.apply(null, MinArr));// 
        for (let index = 0; index < that.xAxisValue.length; index++) {
          if (that.xAxisValue[index] == range[MinSubscript]) {
            MinSubscript = index;
            break;
          }
        }
        //
        // that.xAxisValue.forEach((item, index, arr) => {
        //   if (item === range[MinSubscript]) {
        //     //console.log(item,range[MinSubscript]+'---'+MinSubscript);
        //     // console.log(item,range[MinSubscript]+'')
        //     MinSubscript = index;
        //   }
        // })
        // console.log(range[MinSubscript],MinSubscript+'');
        //
        let MaxArr = [];
        range.map(function(x){ MaxArr.push(Math.abs(x - that.maxValue));});//  
        let MaxSubscript = MaxArr.indexOf(Math.min.apply(null, MaxArr));// 
        for (let j = 0; j < that.xAxisValue.length; j++) {
          if (that.xAxisValue[j] == range[MaxSubscript]) {
            MaxSubscript = j;
            break;
          }
        }
        // that.xAxisValue.forEach((item, index, arr) => {
        //   if (item == range[MaxSubscript]) {
        //     MaxSubscript =index;
        //   }
        // })
        // console.log(range[MinSubscript],MinSubscript);
        //----------------
        let chartDom=document.getElementById(this.id);
        chartDom.setAttribute("style","width:"+this.width+";height:"+this.height+";top:"+'-50px');
        let myChart = this.$echarts.init(chartDom);//
        let option = {
          title : {
            text: '',
            textStyle:{
              //
              color:'#666',
              //,'normal','italic','oblique'
              fontStyle:'normal',
              // 'normal','bold','bolder','lighter',100 | 200 | 300 | 400...
              fontWeight:'normal',
              //
              fontFamily:'PingFangSC-Regular',
              //
              fontSize:10
            }
            ,left:'0px'
          },
          tooltip : {
            trigger: 'axis',
            position: [190, 13],
            axisPointer : {            // 
              type : 'shadow'        // 'line' | 'shadow'
            },
        },
          calculable : true,
          grid: {
            // bottom: '0px',
          //   top:'0px',
          //   containLabel: true
          },
          xAxis : [
            {
                type : 'category',
                data :this.xAxisValue,
                axisTick: {//
                    show: false
                },
                axisLine: {//
                    show:false
                },
                axisLabel: {//
                    show:true,
                    interval:0,  
                    rotate:50,
                    textStyle: {
                        fontSize:10
                    }
                },
                splitLine: {
                    show: false//
                },
            }
          ],
          yAxis : [
            {
              type : 'value',
              axisLine: {//
                show: false
              },
              axisTick: {//
                show: false
              },
              axisLabel: {//
                show:false
              },
              splitLine: {
                show:false
              }
            }
          ],
          series : [
            {
              name:'',
              type:'bar',
              barWidth: '18px',
              barCategoryGap:'0px',
              data:this.PValue,
              label: {
                normal: {
                    show: true,
                    position: 'inside',
                    color:'#000000',
                    icon: 'rect',
                    textStyle: {
                        fontSize:10,
                    }
                }
              },
              itemStyle: {
                normal: {
                  color: function(params) {
                      // console.log(params.name);
                      if(params.name < that.minValue || params.name > that.maxValue){
                        return '#FF971A';
                      }else{
                        return '#93B9FF';
                      }
                  },
                  // borderColor:'#93B9FF',
                }
              }
            },
            {
              type: 'line',
              color:'#FF4040',
              markLine: {
                symbol:'none',
                lineStyle: {
                  type: 'solid',
                  width:'0.5',//
                },
                data: [
                  [
                    {x:24+MinSubscript*20,y:220},
                    {x:24+MinSubscript*20,y:62}//grid24+20
                  ],
                  [
                    {x:24+MaxSubscript*20+19,y:220},
                    {x:24+MaxSubscript*20+19,y:62}//grid24()+20+19()
                  ],
                ]
              },
            }
            // {//
            //   type: 'line',
            //   color:'#FF4040',
            //   markLine: {
            //     symbol:'none',//
            //     data: [
            //         [
            //           {
            //             coord: [MinSubscript, 0],//
            //           }, {
            //             coord: [MinSubscript, PValueMax],//
            //           } 
            //         ],[
            //           {
            //             coord: [MaxSubscript, 0],//
            //           }, {
            //             coord: [MaxSubscript, PValueMax],//
            //           } 
            //         ]
            //     ],
            //     lineStyle: {
            //       type: 'solid',
            //       width:'0.5',//
            //     }
            //   }
            // }
          ]
        };
        // console.log(option);
        //canvas
        window.onresize = myChart.resize;
        myChart.setOption(option);//option
      }
    }
  }
</script>

<style scoped>
.charts{
  top:10px;
}
</style>



