          <el-form-item label=":" prop="EndDateTime">
            <el-date-picker v-model="filter.EndDateTime" type="datetime" placeholder="">
            </el-date-picker>
          </el-form-item>
          <el-form-item label=":" prop="detection">
            <el-select v-model="filter.equipmentId"  placeholder="">
              <el-option
                v-for="item in equipmentOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" prop="productName">
            <el-select v-model="filter.productId"  placeholder="">
              <el-option
                v-for="item in productOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div class="sub-btn">
          <el-button @click="search" :loading="loading"></el-button>
        </div>
      </el-dialog>
    </div>
</template>
<script>
  import CausalityAnalysisView from '../../../View/CausalityAnalysisView';
  export default new CausalityAnalysisView()
</script>
