<template>
  <div :id="id" ></div>
</template>

<script>
  export default {
    data() {
      return {
      }
    },
    props:['width','height','id','xAxisValue','UCLValue','LCLValue','CLValue','PValue'],
    mounted(){
      this.drawLine();
    },
    methods: {
      drawLine(){
        let chartDom=document.getElementById(this.id);
        chartDom.setAttribute("style","width:"+this.width+";height:"+this.height+"");
        let myChart = this.$echarts.init(chartDom);//
        let option = {
          title : {
            text: '(P)',
            textStyle:{
              //
              color:'#666',
              //,'normal','italic','oblique'
              fontStyle:'normal',
              // 'normal','bold','bolder','lighter',100 | 200 | 300 | 400...
              fontWeight:'bold',
              //
              fontFamily:'sans-serif',
              //
              fontSize:12
            }
            ,left:'1%'
          },
          tooltip : {
            trigger: 'axis'
          },
          calculable : true,
          grid: {
            left: '6%',
            right: '6%',
            bottom: '6%',
            containLabel: true
          },
          legend: {
            data:['','P','',''],
            right: '2%',
            top: 'top',
            itemWidth: 30,//
            itemHeight: 15,//
            textStyle: {
              color: '#3a6186',
              fontSize:12,
            }
          },
          xAxis : [
            {
              type : 'category',
              data :this.xAxisValue,
              axisTick: {//
                show: true//
              },
              axisLine: {//
                lineStyle: {
                  type: 'solid',
                  color: '#e7e7e7',//
                  width:'2'//
                }
              },
              axisLabel: {
                textStyle: {
                  color: '#666',//
                },
                interval: 0,
                rotate: 60,
              },
              splitLine: {
                show: false//
                ,type:'dashed'
              },
            }
          ],
          yAxis : [
            {
              type : 'value',
              axisLine: {//
                show: true
              },
              axisTick: {//
                show: true
              },
              axisLabel: {
                textStyle: {
                  color: '#3a6186',//
                }
              },
              splitLine: {

                lineStyle: {
                  color: ['#e7e7e7'],//
                }
              }
            }
          ],
          series : [
            {
              name:'',
              type:'line',
              barWidth: 20,
              data:this.UCLValue,
              step: 'start',
              itemStyle: {
                normal: {
                  color: '#f7b6b9',//
                  label: {
                    show: false,//
                    position: 'top',//
                    textStyle: {
                      color: '#00abf7',//
                      fontSize:16,
                    }
                  }
                }
              },
            },
            {
              name:'P',
              type:'line',
              barWidth: 20,
              data:this.PValue,
              itemStyle: {
                normal: {
                  color: '#3e68ff',//
                  label: {
                    show: false,//
                    position: 'top',//
                    textStyle: {
                      color: '#00abf7',//
                      fontSize:16,
                    }
                  }
                }
              },
            }
            ,
            {
              name:'',
              type:'line',
              markLine: {
                itemStyle: {
                  normal: {
                    color: '#95be80',//
                    label: {
                      show: true,//
                      position: 'top',//
                      textStyle: {
                        color: '#ff4f76',//
                        fontSize:16,
                      }
                    }
                  }
                },
                lineStyle: {
                  normal: {
                    color: '#c23631',
                  },
                },
                silent: true,
                data: [
                  {
                    yAxis: this.CLValue
                  },
                  // {label: {position: 'end'}}
                ]
              }
            }
            ,
            {
              name:'',
              type:'line',
              barWidth: 20,
              step: 'end',
              data:this.LCLValue,
              itemStyle: {
                normal: {
                  color: '#f7b6b9',//
                  label: {
                    show: false,//
                    position: 'top',//
                    textStyle: {
                      color: '#ff4f76',//
                      fontSize:16,
                    }
                  }
                }
              }
            }
          ]
        };
        //canvas
        window.onresize = myChart.resize;
        myChart.setOption(option);//option
      }
    }
  }
</script>

<style scoped>

</style>



