<template>
  <el-dialog
    width="600px"
    :visible.sync="visible"
    :before-close="handleModalImport"
    :close-on-click-modal="false"
    :close-on-press-escape="false">
    <div slot="title"></div>
    <div>
      <el-form :label-position="labelPosition" label-width="90px">
        <el-form-item label=":" size="small">
          <el-upload
          class='image-uploader'
          :multiple='false'
          :auto-upload='true'
          list-type='text'
          :show-file-list='true'
          :before-upload="beforeUpload"
          :drag='true'
          action=''
          :limit="1"
          :on-exceed="handleExceed"
          :http-request="uploadFile"
          >
            <i class="el-icon-upload"></i>
            <div class="el-upload__text"><em></em></div>
            <div class="el-upload__tip" slot="tip">text1MB</div>
          </el-upload>
        </el-form-item>
        <el-form-item label=":" size="small">
          <el-select v-model="formData.batchC" clearable placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select><div class="red">*</div>
        </el-form-item>
      </el-form>
      <div class="sub-btn">
        <el-button @click="handleModalImport(true)"></el-button>
      </div>
    </div>
  </el-dialog>
</template>
<script>
  export default {
    props: {
      visible: {
        type: Boolean,
        default: false
      },
      formData:Object,
    },
    data() {
      return {
        labelPosition: 'left',
        options: [{
          value: '1',
          label: '1'
        }, {
          value: '2',
          label: '2'
        }, {
          value: '3',
          label: '3'
        }, {
          value: '4',
          label: '4'
        }, {
          value: '5',
          label: '5'
        }],
      };
    },
    methods: {
      // isOperate 
      handleModalImport(isOperate) {
        this.$emit('handleModalImport',isOperate)
        // this.$emit('update:visible', false); // 
      },
      beforeUpload (file) {
        console.log('beforeUpload')
        console.log(file.type)
        const isText = file.type === 'application/vnd.ms-excel'
        const isTextComputer = file.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
        return (isText | isTextComputer)
      },
      // 
      handleExceed (files, fileList) {
        this.$message.warning(` 1 `)
      },
      // 
      uploadFile (item) {
        console.log(item)
        const fileObj = item.file
        // FormData 
        const form = new FormData()
        // 
        form.append('file', fileObj)
        form.append('comId', this.comId)
        console.log(JSON.stringify(form))
        // let formTwo = JSON.stringify(form)
        // EnterAPI.iExcel(form).then(response => {
        //   console.log('MediaAPI.upload')
        //   console.log(response)
        //   this.$message.info('' + fileObj.name + '')
        // })
      },
    }
  }
</script>


