    <el-row class="warp">
        <el-col :span="24" class="warp-main box_table" v-loading="loading" element-loading-text="">
            <div v-for="item of TaskBatchList">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span>{{item.productName}}---{{item.workProcessName}}</span>
                        <el-button type="text" size="small" @click="showEditDialog(item)"></el-button>
                    </div>
                    <div class="text item">
                        <el-form label-width="165px">
                            <el-form-item label="">{{item.equipmentName}}</el-form-item>
                            <el-form-item label="">{{item.beginDateTime}}</el-form-item><!--/{{item.endDateTime}}-->
                            <el-form-item label="">{{item.quantity}}</el-form-item>
                            <el-form-item label="" v-if="item.workProcessName === ''">{{item.userDefine1}}</el-form-item>
                            <el-form-item label="" v-if="item.workProcessName === ''">{{item.userDefine2}}</el-form-item>
                        </el-form>
                    </div>
                </el-card>
            </div>
