        <!---->
        <el-dialog  :visible.sync ="editFormVisible"   :before-close="closeEditDialog">
            <div slot="title">{{editForm.workProcessName}}</div>
            <el-form :model="editForm" label-width="130px" :rules="editFormRules" ref="editForm">
            <el-form-item label=":" prop="equipmentClassId">
                <el-select v-model="editForm.equipmentId"  placeholder="">
                <el-option
                    v-for="item in equipmentOptions"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label=":" prop="beginDateTime">
                <el-date-picker v-model="editForm.beginDateTime" placeholder="" value-format="yyyy-MM-dd"></el-date-picker>
            </el-form-item>
            <el-form-item label=":"  prop="quantity" size="small">
                <el-input v-model="editForm.quantity" auto-complete="off" type="number"></el-input>
            </el-form-item>
            <el-form-item label=":" prop="userDefine1" v-if="editForm.workProcessName == ''">
                <el-input v-model="editForm.userDefine1" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label=":" prop="userDefine2" v-if="editForm.workProcessName == ''">
