<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-main box_table" v-loading="loading" element-loading-text="">
      <!---->
      <div class="table_tool">
          <div class="box_search">
            <el-form ref="form" :model="filter">
              <el-input v-model="filter.Keyword" @keyup.enter.native="search" size="mini" placeholder="/"></el-input>
              <el-button type="primary" icon="el-icon-search" size="mini" @click="search" ></el-button>
              <el-button type="primary" icon="el-icon-circle-plus-outline" style="float: right" size="mini" @click="showAddDialog" ></el-button>
            </el-form>
          </div>
      </div>
      <!---->
      <el-table :data="datalist" highlight-current-row @selection-change="selectInfos" stripe
                style="width: 100%;">
        <el-table-column type="index" width="60">
        </el-table-column>
        <el-table-column prop="userName" label=""  sortable>
        </el-table-column>
        <el-table-column prop="userCode" label="" sortable>
        </el-table-column>
        <el-table-column prop="roleID" label=""   :formatter="formattype" sortable>
        </el-table-column>
        <el-table-column prop="eMail" label=""  sortable>
        </el-table-column>
        <el-table-column prop="telephone" label=""  sortable>
        </el-table-column>
        <el-table-column prop="mobilePhone" label=""  sortable>
        </el-table-column>
        <el-table-column label="" width="180">
          <template slot-scope="scope">
            <el-button size="small"  v-if="scope.row.userName!=''"  @click="showEditDialog(scope.row)"></el-button>
            <el-button type="danger"  v-if="scope.row.userName!=''"   @click="del(scope.row.userID)" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-col :span="24" class="toolbar">
        <Pagination
          @handleCurrentChange="handleCurrentChange"
          @handleSizeChange="maxResultCountSelect"
          :page-size="maxResultCount"
          :page-sizes="pageSelectList"
          :total="totalCount">
        </Pagination>
      </el-col>
      <!---->
      <el-dialog  :visible.sync ="addFormVisible"  :before-close="closeAddDialog">
        <div slot="title"></div>
        <el-form :model="addForm" label-width="120px" :rules="editFormRules" ref="addForm">
          <el-form-item label=":" prop="userCode">
            <el-input v-model="addForm.userCode" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="userName">
            <el-input v-model="addForm.userName" auto-complete="off"></el-input>
          </el-form-item>

          <el-form-item label=":" prop="roleID">
            <el-select v-model="addForm.roleID" multiple placeholder="">
              <el-option
                v-for="item in typeoptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" prop="eMail">
            <el-input v-model="addForm.eMail" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="telephone">
            <el-input v-model="addForm.telephone" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="mobilePhone">
            <el-input v-model="addForm.mobilePhone" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="closeAddDialog"></el-button>
          <el-button type="primary" @click.native="add" :loading="loading"></el-button>
        </div>
      </el-dialog>

      <el-dialog  :visible.sync ="editFormVisible"   :before-close="closeEditDialog">
        <div slot="title"></div>
        <el-form :model="editForm" label-width="120px" :rules="editFormRules" ref="editForm">
          <el-form-item label=":" prop="userCode">
            <el-input v-model="editForm.userCode" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="userName">
            <el-input v-model="editForm.userName" auto-complete="off"></el-input>
          </el-form-item>

          <el-form-item label=":" prop="roleID">
            <el-select v-model="editForm.roleID" multiple  placeholder="">
              <el-option
                v-for="item in typeoptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label=":" prop="eMail">
            <el-input v-model="editForm.eMail" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="telephone">
            <el-input v-model="editForm.telephone" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="mobilePhone">
            <el-input v-model="editForm.mobilePhone" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="closeEditDialog"></el-button>
          <el-button type="primary" :loading="loading" @click.native="edit"></el-button>
        </div>
      </el-dialog>
    </el-col>
  </el-row>
</template>
<script>
import UserView from '../../../View/UserView'
export default new UserView();
</script>
<style lang="stylus" scope>
@import '../../../common/common.stylus'
</style>



