<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-main box_table" v-loading="loading" element-loading-text="">
      <!---->
      <div class="table_tool">
        <div class="box_search">
          <el-form ref="form" :model="filter">
            <el-input v-model="filter.parameterKeyword" placeholder="" @keyup.enter.native="search" size="mini"></el-input>
            <el-button type="primary" icon="el-icon-search" size="mini" @click="search" ></el-button>
            <el-button type="primary" icon="el-icon-circle-plus-outline" style="float: right"  size="mini" @click="showAddDialog" ></el-button>
          </el-form>
        </div>
      </div>
      <!---->
      <el-table class="tableCss" :data="datalist" highlight-current-row @selection-change="selectInfos" stripe
                style="width: 100%;">
        <el-table-column type="index" width="60">
        </el-table-column>
        <el-table-column prop="parameterCode" label="" sortable>
        </el-table-column>
        <el-table-column prop="parameterName" label="" sortable>
        </el-table-column>
        <el-table-column prop="parameterDataType" label="" sortable>
        </el-table-column>
        <el-table-column prop="parameterValuePrecision" label="" sortable>
        </el-table-column>
        <el-table-column prop="parameterValueType" label="" sortable>
        </el-table-column>
        <el-table-column prop="standardValue" label="" sortable>
        </el-table-column>
        <el-table-column prop="minValue" label="" sortable>
        </el-table-column>
        <el-table-column prop="maxValue" label="" sortable>
        </el-table-column>
        <el-table-column label="" width="150">
          <template slot-scope="scope">
            <el-button size="small" @click="showEditDialog(scope.row)"></el-button>
            <el-button type="danger" @click="del(scope.row.parameterID)" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-col :span="24" class="toolbar">
        <Pagination
          @handleCurrentChange="handleCurrentChange"
          @handleSizeChange="maxResultCountSelect"
          :page-size="maxResultCount"
          :page-sizes="pageSelectList"
          :total="totalCount">
        </Pagination>
      </el-col>
      <!---->
      <el-dialog  :visible.sync ="addFormVisible"  :before-close="closeAddDialog">
        <div slot="title"></div>
        <el-form :model="addForm" label-width="130px" :rules="editFormRules" ref="addForm">
          <el-form-item label=":" prop="parameterCode">
            <el-input v-model="addForm.parameterCode" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="parameterName">
            <el-input v-model="addForm.parameterName" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="parameterDataType">
            <el-select v-model="addForm.parameterDataType"  placeholder="">
              <el-option
                v-for="item in parameterDataTypeOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" prop="parameterValuePrecision">
            <el-input type="number" v-model="addForm.parameterValuePrecision" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label=":"  prop="parameterValueType" size="small">
            <el-select v-model="addForm.parameterValueType"  placeholder="">
              <el-option
                v-for="item in parameterValueTypeOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" prop="standardValue">
            <el-input type="number" v-model="addForm.standardValue" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="minValue">
            <el-input type="number" v-model="addForm.minValue" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="maxValue">
            <el-input type="number" v-model="addForm.maxValue" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="closeAddDialog"></el-button>
          <el-button type="primary" @click.native="add" :loading="loading"></el-button>
        </div>
      </el-dialog>

      <el-dialog  :visible.sync ="editFormVisible"   :before-close="closeEditDialog">
        <div slot="title"></div>
        <el-form :model="editForm" label-width="130px" :rules="editFormRules" ref="editForm">
          <el-form-item label=":" prop="parameterCode">
            <el-input v-model="editForm.parameterCode" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="parameterName">
            <el-input v-model="editForm.parameterName" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="parameterDataType">
            <el-select v-model="editForm.parameterDataType"  placeholder="">
              <el-option
                v-for="item in parameterDataTypeOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" prop="parameterValuePrecision">
            <el-input type="number" v-model="editForm.parameterValuePrecision" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label=":"  prop="parameterValueType" size="small">
            <el-select v-model="editForm.parameterValueType"  placeholder="">
              <el-option
                v-for="item in parameterValueTypeOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" prop="standardValue">
            <el-input type="number" v-model="editForm.standardValue" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="minValue">
            <el-input type="number" v-model="editForm.minValue" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="maxValue">
            <el-input type="number" v-model="editForm.maxValue" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="closeEditDialog"></el-button>
          <el-button type="primary" @click.native="edit"></el-button>
        </div>
      </el-dialog>
    </el-col>
  </el-row>
</template>
<script>
  import ParameterView from '../../../View/ParameterView';
  export default new ParameterView()
</script>
<style lang="stylus" scope>
  @import '../../../common/common.stylus'
</style>



