<template>
    <el-dialog
        title=""
        :visible.sync="visible"
        :rowId="rowId"
        width="30%"
        :before-close="handleModalDel">
        <span>?</span>
        <span slot="footer" class="dialog-footer">
            <el-button @click="handleModalDel(false)"> </el-button>
            <el-button type="primary" @click="handleModalDel(true)"> </el-button>
        </span>
    </el-dialog>
</template>

<script>
  export default {
    props:{
        visible:{
            type: Boolean,
            default: false
        },
        rowId:{
            type: Number,
            default: 0
        }
    },
    methods: {
      handleModalDel(isdel) {
        this.$emit('handleModalDel',isdel)
        /* this.$emit('update:visible', false) //  */
      },
    }
  };
</script>



